label {
  font-size: 0.85em;
  font-weight: bold;
  display: block;
}

input {
  padding: .5rem 1rem;
  border-radius: 3px;
  border: 1px solid $light-gray;
  box-shadow: 0 2px 5px rgba(0,0,0,.1);
  margin-bottom: 1rem;
}

input:not(.phonepart) {
  width: 100%;
}

input[type=submit] {
  font-family: $brand-font-stack;
  font-size: 1em;
  display: inline-block;
  color: #fff;
  background-color: #007c04;
  padding: .5rem;
  margin-top: 1rem;
  height: auto;
}

.asterisk {
  color: red;
}